import { ChatBubbleOutlineOutlined,
        FavoriteBorderOutlined,
        FavoriteOutlined,
        Height,
        ShareOutlined } from "@mui/icons-material";
import { Box, Divider, IconButton, Typography, useTheme } from "@mui/material";
import FlexBetween from "components/FlexBetween";
import Friend from "components/Friend";
import WidgetWrapper from "components/WidgetWrapper";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setPost } from "state";


const PostWidget = ({
    postId,
    postUserId,
    name,
    description,
    location,
    picturePath,
    userPicturePath,
    likes,
    comments
}) => {
    const [isComments, setIsComments] = useState(false);
    const dispatch = useDispatch();
    const token = useSelector((state)=> state.token);
    const loggedInUserId = useSelector((state)=> state.user._id);
    const isLiked = Boolean(likes[loggedInUserId]); // because likes are type of map if we see in the db model there is userId and true in it
    const likeCount = Object.keys(likes).length;

    const { palette } = useTheme();
    
    const main = palette.neutral.main;
    const primary = palette.primary.main;

    const patchLikes = async () => {
        const { REACT_APP_SERVER_URL } = process.env;
        // const response = await fetch(`http://localhost:3001/posts/${postId}/like`, {
        const response = await fetch(`${REACT_APP_SERVER_URL}/posts/${postId}/like`, {
            method: "PATCH",
            headers: {
                Authorization: `Bearer ${token}`,
                "Content-Type": "application/json"
            },
            body: JSON.stringify({userId: loggedInUserId})
        });
        const updatedPost = await response.json();
        dispatch(setPost({post: updatedPost}));
    }
    const { REACT_APP_SERVER_URL } = process.env;
    return (
        
        <WidgetWrapper m="2rem">
            <Friend
                friendId={postUserId}
                name = {name}
                subtitle={location}
                userPicturePath={userPicturePath}
            />
            <Typography color={main} sx={{mt:"1rem"}}>
                {description}
            </Typography>
            {picturePath && 
            (<img 
                width="100%"
                height= "auto"
                alt="post"
                style={{borderRadius: "0.75rem", marginTop: "0.75rem" }}
                // src={`http://localhost:3001/assets/${picturePath}`}
                src={`${REACT_APP_SERVER_URL}/assets/${picturePath}`}

            />)
            }
            <FlexBetween mt="0.25rem">
                <FlexBetween gap="1rem">
                    <FlexBetween gap="0.3rem">
                        <IconButton onClick={patchLikes}>
                            {isLiked ? (
                                <FavoriteOutlined sx={{color: primary}} />
                            ):(
                                <FavoriteBorderOutlined />
                            )}
                        </IconButton>
                        <Typography>{likeCount}</Typography>
                    </FlexBetween>

                    <FlexBetween gap="0.3rem">
                        <IconButton onClick={()=> setIsComments(!isComments)}>
                            <ChatBubbleOutlineOutlined />
                        </IconButton>
                        <Typography>{comments.length}</Typography>
                    </FlexBetween>

                </FlexBetween>
                <IconButton>
                    <ShareOutlined />
                </IconButton>
            </FlexBetween>
            {isComments && (
                <Box mt="0.5rem">
                    {comments.map((comment, i) => (
                        <Box key={`${name}-${i}`}> 
                        <Divider />
                        <Typography sx={{color:main, m:"0.5rem 0", pl:"1rem"}}>
                            {comment}
                        </Typography>
                        </Box>
                    ))}
                    <Divider />
                </Box>
            )}
        </WidgetWrapper>
     );
}
 
export default PostWidget;
